\n                                    type=\"number\"\n                                    step=\"0.01\"\n                                    name=\"shipping\"\n                                >\n                            </div>\n                            <div class=\"form-group mt-2\">\n                                <label for=\"price\" class=\"m-2\">Price</label>\n                                <input\n                                    class=\"form-control\"\n                                    id=\"price\"\n                                    v-model=\"price\"\n                                    type=\"number\"\n                                    step=\"0.01\"\n                                    name=\"price\"\n                                >\n                            </div>\n                            <div class=\"form-group mt-2\">\n                                <label for=\"volume\" class=\"m-2\">Volume</label>\n                                <input\n                                    class=\"form-control\"\n                                    id=\"volume\"\n                                    v-model=\"volume\"\n                                    type=\"number\"\n                                    step=\"0.01\"\n                                    name=\"volume\"\n                                    min=\"0.01\"\n                                    max=\"15\"\n                                >\n                            </div>\n                            <div class=\"form-group mt-2\">\n                                <label for=\"year\" class=\"m-2\">Year</label>\n                                <input\n                                    class=\"form-control\"\n                                    id=\"year\"\n                                    v-model=\"year\"\n                                    type=\"number\"\n                                    step=\"1\"\n                                    name=\"year\"\n                                    min=\"1970\"\n                                    max=\"2022\"\n                                >\n                            </div>\n                            <div class=\"text-center\">\n                                <button type=\"submit\" class=\"btn btn-primary mt-4\">Calculate</button>\n                            </div>\n                        </form>\n                    </div>\n                </div>\n                <div class=\"result col-lg-4 col-md-4 col-sm-4\">\n                    <div class=\"col\">\n                        <div class=\"row\">\n                            <div class=\"col\">\n                                <p>Excise:</p>\n                                <p>Import duty: </p>\n                                <p>VAT: </p>\n                            </div>\n                            <div class=\"col\">\n                                <div class=\"calculate text-end\">\n                                    <p><b>{{ car.excise }}</b></p>\n                                    <p><b>{{ car.importDuty }}</b></p>\n                                    <p><b>{{ car.vat }}</b></p>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col-lg-12 col-md-12 col-sm-12\">\n                    <h3 class=\"box-title mt-5 mb-4\">Information</h3>\n                    <div class=\"table-responsive\">\n                        <table class=\"table table-striped table-product\">\n                            <tbody>\n                                <tr>\n                                    <td width=\"390\">Mark</td>\n                                    <td>{{ car.manufacturer }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Model</td>\n                                    <td>{{ car.mark }}</td>\n                                </tr>\n                                <tr>\n                                    <td>VIN</td>\n                                    <td>{{ car.vin }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Fuel</td>\n                                    <td>{{ car.fuel }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Volume</td>\n                                    <td>{{ car.volume }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Year</td>\n                                    <td>{{ car.year }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Price</td>\n                                    <td>{{ car.price }}</td>\n                                </tr>\n                                <tr>\n                                    <td>Country</td>\n                                    <td>{{ car.country }}</td>\n                                </tr>\n                            </tbody>\n                        </table>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n<script>\nimport { mapActions, mapGetters, mapMutations } from 'vuex';\n\nexport default {\n    name: 'car',\n    computed: {\n        ...mapGetters({\n            car: \"car/car\"\n        }),\n        shipping: {\n            get() {\n                return this.$store.state.car.car.shippingCost;\n            },\n            set(value) {\n                this.$store.commit(\"car/setCarShippingCost\", value);\n            },\n        },\n        price: {\n            get() {\n                return this.$store.state.car.car.price;\n            },\n            set(value) {\n                this.$store.commit(\"car/setCarPrice\", value);\n            },\n        },\n        volume: {\n            get() {\n                return this.$store.state.car.car.volume;\n            },\n            set(value) {\n                this.$store.commit(\"car/setCarVolume\", value);\n            },\n        },\n        year: {\n            get() {\n                return this.$store.state.car.car.year;\n            },\n            set(value) {\n                this.$store.commit(\"car/setCarYear\", value);\n            },\n        },\n    },\n    methods: {\n        ...mapActions({\n            getCar: \"car/getCar\",\n            customPayments: \"car/customPayments\"\n        }),\n        ...mapMutations({\n            setCarClear: \"car/setCarClear\",\n        }),\n        async submitForm(ev) {\n            this.customPayments({\n                price: this.price,\n                volume: this.volume,\n                year: this.year,\n                fuel: this.car.fuel,\n                cost: this.shipping\n            });\n        }\n    },\n    mounted() {\n        this.setCarClear();\n        this.getCar({ id: this.$route.params.id });\n    }\n}\n</script>\n\n<style scoped>\n    .calculateForm {\n        max-width: 300px;\n    }\n    body {\n        background-color: #edf1f5;\n        margin-top:20px;\n    }\n    .card {\n        margin-bottom: 30px;\n    }\n    .card {\n        position: relative;\n        display: flex;\n        flex-direction: column;\n        min-width: 0;\n        word-wrap: break-word;\n\t    background-color: #d7d7d745;\n        background-clip: border-box;\n        border: 0 solid transparent;\n        border-radius: 0;\n    }\n    .card-title {\n        margin-top: 25px;\n        margin-left: 25px;\n        margin-bottom: 20px;\n    }\n    .result {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n    }\n    .calculate {\n        margin-right: 20px;\n    }\n    .calculator {\n        padding-left: 50px;\n        padding-right: 50px;\n    }\n</style>\n"],"sou